<template lang="html">
  <section class="lottery-users">
    <!-- 条件筛选 -->
    <FilterArea />
    <!-- 表格数据 -->
    <div class="table-list">
      <!-- 表格 -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" />

        <el-table-column prop="name" label="所属彩种" />
        <el-table-column prop="code" label="会员账号" />
        <el-table-column prop="balls" label="投注期号" />
        <el-table-column prop="money" label="投注批次" />
        <el-table-column prop="scale" label="投注玩法" />
        <el-table-column prop="effect" label="玩法子规则" />
        <el-table-column prop="effect" label="投注内容" />
        <el-table-column prop="effect" label="投注时间" />
        <el-table-column prop="effect" label="投注金额" />
        <el-table-column prop="effect" label="输赢" />
        <el-table-column prop="effect" label="返水金额" />
        <el-table-column prop="effect" label="结算状态" />

        <el-table-column prop="operations" label="操作" :min-width="200">
          <template slot-scope="scope">
            <el-button @click="showTip" type="primary" size="mini">修改设置</el-button>
            <el-button @click="showTip" type="primary" size="mini">删除设置</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <BasePagination @on-change="handlePaginationChange" :pageTotal="pageTotal" httpURL="nuknown" />
    </div>
  </section>
</template>

<script>
import FilterArea from '@/components/others/FilterArea'
import BasePagination from '@/components/base/BasePagination'

export default {
  name: 'LotteryUsersInfo',
  components: {
    FilterArea,
    BasePagination
  },
  data () {
    return {
      tableData: [
        // { name: '江苏快3', code: 'jsfast3', balls: 3, money: 12345, scale: 0.88, effect: true },
        // { name: '广东11选5', code: 'jsfast3', balls: 5, money: 12345, scale: 1.18, effect: true },
        // { name: '北京时时彩', code: 'jsfast3', balls: 3, money: 12345, scale: 0.88, effect: true }
      ],
      pageTotal: 0
    }
  },
  created () {
    // this.fetchUsersList()
  },
  mounted () {
    this.$notify({
      title: '提示',
      message: '页面使用模拟数据，接口正在调试中...',
      type: 'warning',
      duration: 8000
    })
  },
  methods: {
    showTip () {
      this.$message.warning('该功能正在开发中...')
    },
    // 分页变化时，更新数据
    handlePaginationChange (data) {
      this.tableData = data
    },
    fetchUsersList (page) {
      this.$httpAPI.fetchUsersList({ params: { pageNo: 1, pageSize: 3 } }).then(response => {
        this.tableData = response.data.data
        this.pageTotal = 3
      }).catch(error => console.log(error))
    }
  }
}
</script>
